import API from './fetchPictures';
import Notiflix from 'notiflix';
import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";


const refs = {
    searchForm: document.querySelector('#search-form'),
    galleryContainer: document.querySelector('.gallery'),
    loadMoreBtn: document.querySelector('.load-more')
};

refs.searchForm.addEventListener('submit', onSearch);
refs.loadMoreBtn.addEventListener('click', onLoadMore);

let searchQuery = '';
let totalHits = '';

function onSearch(e) {
    e.preventDefault();
    searchQuery = e.target.searchQuery.value;
    resetMarkup();
    if (searchQuery === '') {
        // resetMarkup();
        location. reload()
        return
    };
    API.fetchPictures(searchQuery)
        .then(renderMarkup)
        .catch(onFetchError)
    
    e.target.searchQuery.value = '';
};

function resetMarkup() {
    refs.galleryContainer.innerHTML = '';
};

function renderMarkup(pictures) {
    // console.log(pictures)
    totalHits = pictures.totalHits
    if (pictures.hits.length === 0) {
        Notiflix.Notify.warning('Sorry, there are no images matching your search query. Please try again.');   
    };
    
    const createdElements = pictures.hits.map(el => {
        const createdEl = `
        <div class="gallery photo-card">
            <img src="${el.webformatURL}" alt="${el.tags}" loading="lazy" />
            <div class="info">
                <p class="info-item">
                <b>Likes: ${el.likes}</b>
                </p>
                <p class="info-item">
                <b>Views: ${el.views}</b>
                </p>
                <p class="info-item">
                <b>Comments: ${el.comments}</b>
                </p>
                <p class="info-item">
                <b>Downloads: ${el.downloads}</b>
                </p>
            </div>
            </div>
        `;
        return createdEl;
    }).join('');

    refs.galleryContainer.innerHTML = createdElements;
};

function onFetchError() {
    Notiflix.Notify.failure('WTF?!');
};

function onLoadMore() {
    API.fetchPictures(searchQuery)
        .then(renderMarkup)
        .catch(onFetchError)
    console.log(totalHits)
};

new SimpleLightbox(".gallery a", {
    captionSelector: 'img',
    captionsData: 'alt',
    captionPosition: 'bottom',
    captionDelay: 250,
    scrollZoom: false,
});